
/**
 * webData {object} : 包含属性 
 * webName<string> 
 * url<string> 
 * icon<string> 
 * baseUrl<string>
 * description<string>
 */
export function collectWeb(webData){
  // 依据网址判断 localStorage 中是否已经含有改网址

  let storage = localStorage.getItem("collWeb")
  storage ? add() : initAdd()
  
  function add(){

    let hasUrl = checkUrl(webData.url)

    hasUrl ? addWeb() : alert("该网址已添加")

    function addWeb() {
      let storageObj = JSON.parse(storage)
      storageObj.push(webData)
      let strData = JSON.stringify(storageObj)
      localStorage.setItem('collWeb',strData)
    }
  }

  // 判断该 url 是否已经存在
  function checkUrl(url){
    let storageObj = JSON.parse(storage)
    let hasUrl = false;
    hasUrl = storageObj.every(item => item.url !== url);
    return hasUrl
  }


  // 初始添加
  function initAdd(){
    let arr = []
    arr.push(webData)
    let strData = JSON.stringify(arr)
    localStorage.setItem("collWeb",strData)
  }

}

