<template>
  <div style="height: 100%" class="appContainer">
    <div class="sideBar">
      <side-bar></side-bar>
    </div>
    <main>
      <div class="mainBox">
        <search class="searchBox"></search>
        <div class="yedu">人民日报夜读</div>
        <web-container class="webs"></web-container>
      </div>
    </main>
    <div v-if="false" class="rightBox">
      <!-- <i class="iconfont icon-toTop"></i> -->
    </div>
  </div>
</template>

<script>
import Search from "@/components/Search"
import WebContainer from "@/views/WebContainer"
import SideBar from "@/views/SideBar"
import userGetWebdata from '@/hooks/userGetWebData.js'

import { computed, ref } from '@vue/reactivity'
import { provide } from "vue"
export default {
  setup: function () {
    // 获取网址数据的异步函数，放在 mounted 钩子中调用
    let { getWebData } = userGetWebdata()

    let tagindex = ref(0)
    provide("tagIndex", tagindex)
    provide("changeTagIndex", function (index) {
      tagindex.value = index
    })
    return {
      getWebData
    }
  },
  data() {
    return {
      webdata: {}
    }
  },
  async mounted() {
    let webdata;
    try {
      webdata = await this.getWebData()
    } catch (e) {
      console.log("获取网页数据失败", e)
      alert("获取网址数据失败，请刷新页面重试")
    }
    this.webdata = webdata
  },
  provide() {
    return {
      webdata: computed(() => this.webdata)
    }
  },
  components: {
    SideBar,
    Search,
    WebContainer
  }
}
</script>

